import { connect } from 'react-redux';
import MathchCard from '../../component/matchcards/MatchCard';
import { useState, useEffect } from 'react';
import Loading from '../../component/loading/Loading';
import './match.scss';
import fetchData from '../../fetchData';
import NotFound from '../../component/NotFound/NotFound';
const Matches = (props) => {
   
    const [data,setData] = useState(null);
    const [isLoading,setLoading] = useState(true);
    const [notFound,setNoutFound] = useState(false);
   useEffect( ()=>{
    return async () => {
        try{
            const matches = await fetchData('/matches');
            setLoading(false);
            setData(matches);
            console.log(matches);
        }catch(err){
            setNoutFound(true);
            setLoading(false);  
            console.log(notFound);
        }
    } 
},[]);
   
    return ( 
            <>
           {isLoading? <Loading/>: (
               !data? <NotFound/>:
                <div className= {`match  ${props.dark?'dark':''}` } >
                    <div className="matchWrapper">
                        <h1 className="matchTitle">UpComming Matches</h1>
                        <div className="matchCardContainer">
                            {
                                data.map((value,key)=>{
                                    return (<MathchCard dark={props.dark} key={key} match ={data[key]}/>)   
                                })
                            }
                        </div>

                    </div>
                </div>
           )
           
            
            }
        </>
     );
}
const  mapStateToProps = (state) =>{
    return {
        dark:state.dark,
        matches : state.matches
    }
}

export default connect(mapStateToProps)(Matches);